<template>
<tr>
  <td>{{ group }}</td>
  <td v-if="verified"><span class="icon has-text-success"><svg-icon type="mdi" :path="okIcon" :size="24">&nbsp;</svg-icon></span></td>
  <td v-else><span class="icon has-text-danger"><svg-icon type="mdi" :path="koIcon" :size="24">&nbsp;</svg-icon></span></td>
</tr>
</template>

<script>
import SvgIcon from '@jamescoyle/vue-icon';
import {mdiAccountMultipleCheck} from  '@mdi/js';
import {mdiAccountMultipleRemove} from  '@mdi/js';

export default {
  name: "AccountGroupItem",
  components: {
    SvgIcon
  },
  computed: {
    okIcon() {
      return mdiAccountMultipleCheck;
    },
    koIcon() {
      return mdiAccountMultipleRemove;
    }
  },
  props: {
    group: {
      type: String,
      required: true
    },
    verified: {
      type: Boolean,
      required: true
    }
  }
}
</script>
