<template>
  <section>
    <div v-if="isLoading">
      <base-spinner></base-spinner>
    </div>
    <base-modal :show="!!error" color="is-danger" title="Verification Error" @close="handleError">
      {{ error }}
    </base-modal>
    <base-modal :show="success" color="is-success" title="Verification Succeeded" @close="handleSuccess">
      The group has been added to your account.
    </base-modal>
    <div class="card">
      <div class="card-header">
        <p class="card-header-title">
          Add Group
        </p>
      </div>
      <div class="card-content content">
        <form @submit.prevent="doSubmit">
          <div class="field is-horizontal">
            <div class="field-label is-normal">
              <label class="label" for="groupName">Group Name</label>
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control is-expanded has-icons-left">
                  <input class="input" id="groupName" type="text" placeholder="FSEconomy Group Name" v-model.trim="groupName">
                  <span class="icon is-small is-left">
                  <svg-icon type="mdi" :path="groupIcon">&nbsp;</svg-icon>
                </span>
                </div>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label">
              <!-- Left empty for spacing -->
            </div>
            <div class="field-body">
              <div class="field">
                <article class="message is-danger" v-if="!formIsValid">
                  <div class="message-body">
                    Group Name must not be empty.
                  </div>
                </article>
              </div>
            </div>
          </div>
          <div class="field is-horizontal">
            <div class="field-label">
              <!-- Left empty for spacing -->
            </div>
            <div class="field-body">
              <div class="field">
                <div class="control">
                  <button class="button is-primary">Submit</button>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    </div>
  </section>
</template>

<script>
import BaseSpinner from "@/components/ui/BaseSpinner";
import BaseModal from "@/components/ui/BaseModal";
import SvgIcon from '@jamescoyle/vue-icon';
import { mdiAccountMultiple } from '@mdi/js';

export default {
  name: "AccountAddGroup",
  components: {
    BaseSpinner,
    BaseModal,
    SvgIcon
  },
  computed: {
    groupIcon() {
      return mdiAccountMultiple;
    }
  },
  data() {
    return {
      isLoading: false,
      success: false,
      error: null,
      groupName: '',
      formIsValid: true,
    };
  },
  methods: {
    handleError() {
      this.error = null;
    },
    handleSuccess() {
      this.success = false;
      this.groupName = '';
      this.formIsValid = true;
    },
    async doSubmit() {
      if (this.groupName === '') {
        this.formIsValid = false;
        return;
      }
      this.isLoading = true;
      const actionPayload = {
        groupName: this.groupName,
      };
      try {
        await this.$store.dispatch('auth/addGroup', actionPayload);
        this.success = true;
      } catch (error) {
        this.error = error.message || `Failed to add group ${this.groupName} to your account, try later.`;
      }
      this.isLoading = false;
    }
  }
}
</script>
