<template>
  <section>
    <div v-if="isLoading">
      <base-spinner></base-spinner>
    </div>
    <base-modal :show="!!error" color="is-danger" title="Verification Error" @close="handleError">
      {{ error }}
    </base-modal>
    <base-modal :show="success" color="is-success" title="Verification Succeeded" @close="handleSuccess">
      Some or all of your groups have been verified.
    </base-modal>
    <base-modal :show="help" title="Group Verification" @close="toggleHelp">
      <div class="content">
        <p>
          One or several groups associated with your account have not yet been verified. Stormrose uses micro payments
          within the <a href="https://server.fseconomy.net/">FSEconomy Game World</a> to verify groups. Please check the
          <a href="https://server.fseconomy.net/banksummary.jsp">Payment Log</a> of the respective group for such a
          micro
          payment. It was sent when the group was registered for your account, and should look like this:
        </p>
        <figure class="image">
          <img src="../../assets/payment.png" alt="payment illustration"/>
        </figure>
        <p>
          Please use the FSEconomy Game World <a href="https://server.fseconomy.net/banksummary.jsp">banking page</a>
          to return this micro payment to the group it came from. This is part of an automated process so please:
        </p>
        <ul>
          <li>send it back in one transaction &amp; make sure the amount is correct</li>
          <li>select the destination group from the drop-down list that appears when you start typing the name</li>
          <li>include the verification token (alphanumeric token provided in the &ldquo;Comments&rdquo; field) in the
            Comments field.
          </li>
        </ul>
        <p>
          Once the transaction is completed, use the Verify Groups button to confirm the transaction and complete the
          verification process.
        </p>
      </div>
    </base-modal>
    <div class="card">
      <div class="card-header">
        <p class="card-header-title">
          Groups Associated With Your Account
        </p>
      </div>
      <div class="card-content content">
        <table class="table">
          <thead>
          <tr>
            <th>Group</th>
            <th>Verification Status</th>
          </tr>
          </thead>
          <tbody>
          <account-group-item v-for="group in groups"
                              :group="group.group"
                              :verified="!!group.verified"
                              :key="group.group"
          >
          </account-group-item>
          </tbody>
        </table>
        <div class="field is-grouped" v-if="!allVerified">
          <div class="control">
            <button class="button is-primary" @click="validateGroups">Verify Groups</button>
          </div>
          <div class="control">
            <button class="button is-light" @click="toggleHelp">Help</button>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import AccountGroupItem from "@/components/account/AccountGroupItem";
import BaseSpinner from "@/components/ui/BaseSpinner";
import BaseModal from "@/components/ui/BaseModal";

export default {
  name: "AccountGroupList",
  components: {
    AccountGroupItem,
    BaseSpinner,
    BaseModal
  },
  computed: {
    groups() {
      const profile = this.$store.getters['auth/profile'];
      const groupData = profile.groups;
      groupData.sort((a, b) => a.group.localeCompare(b.group));
      return groupData;
    },
    allVerified() {
      const profile = this.$store.getters['auth/profile'];
      let verified = true;
      for (const group of profile.groups) {
        verified = verified && !!group.verified;
      }
      return verified;
    }
  },
  data() {
    return {
      isLoading: false,
      success: false,
      error: null,
      help: false
    }
  },
  methods: {
    handleError() {
      this.error = null;
    },
    handleSuccess() {
      this.success = false;
    },
    async validateGroups() {
      this.isLoading = true;
      try {
        await this.$store.dispatch('auth/verifyGroups');
        this.success = true;
      } catch (error) {
        this.error = error.message || 'Failed to verify your groups, try later.';
      }
      this.isLoading = false;
    },
    toggleHelp() {
      this.help = !this.help;
    }
  }
}
</script>
